import React, { useState } from 'react';
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import Checkbox from '@mui/material/Checkbox';
import { DatePicker } from '@/components';

function FormCustomerInfo(props) {
    const { infoStates, infoHandlers, infoErrors } = props;
    const { legalForm, iin, gender, surname, firstname, lastname, adress } = infoStates;

    return (
        <Box
            component="form"
            sx={{
                '& .MuiTextField-root': { mr: 5, mt: 2, mb: 2, width: '38ch' }
            }}
            noValidate
            autoComplete="off"
        >
            <div>
                <FormControl className="MuiTextField-root">
                    <InputLabel id="select-legal-form"> </InputLabel>
                    <Select
                        labelId="select-legal-form"
                        id="select-legal-form"
                        value={legalForm}
                        label=" "
                        onChange={infoHandlers.handleLegalform}
                    >
                        <MenuItem value="individual"> </MenuItem>
                        <MenuItem value="legalEntity"> </MenuItem>
                    </Select>
                </FormControl>
                <FormControl className="MuiTextField-root">
                    <Box>
                        <Checkbox defaultChecked sx={{ '& .MuiSvgIcon-root': { fontSize: 28 } }} />
                         
                    </Box>
                </FormControl>
            </div>
            <div>
                <TextField id="customer-iin" {...infoErrors.checkIin} label="" value={iin} onChange={infoHandlers.handleIin} />
                <FormControl className="MuiTextField-root">
                    <InputLabel id="form-select-gender"></InputLabel>
                    <Select
                        labelId="form-select-gender"
                        id="form-select-gender"
                        value={gender}
                        label=""
                        onChange={infoHandlers.handleGender}
                    >
                        <MenuItem value="man"></MenuItem>
                        <MenuItem value="woman"></MenuItem>
                    </Select>
                </FormControl>
                <DatePicker />
            </div>
            <div>
                <TextField
                    id="customer-surname"
                    {...infoErrors.checkRuString}
                    label=""
                    value={surname}
                    onChange={infoHandlers.handleSurname}
                />
                <TextField
                    id="customer-firstname"
                    {...infoErrors.checkRuString}
                    label=""
                    value={firstname}
                    onChange={infoHandlers.handleFirstname}
                />
                <TextField
                    id="customer-lastname"
                    {...infoErrors.checkRuString}
                    label=""
                    value={lastname}
                    onChange={infoHandlers.handleLastname}
                />
            </div>
            <div>
                <TextField
                    style={{ background: 'red', width: '124.5ch' }}
                    id="customer-adress"
                    {...infoErrors.checkRuString}
                    label=""
                    value={adress}
                    onChange={infoHandlers.handleAdress}
                />
            </div>
        </Box>
    );
}

export default FormCustomerInfo;
